.card-container {
  margin-left: 20rem;
  margin-top: 2rem;
  z-index: -2;
  display: flex;
  flex-wrap: wrap;
}
.card {
  margin: 20px;
  padding: 20px;
  border: 2px solid #ededed;
  cursor: pointer;
}
.card-img {
  width: 13rem;
  margin-bottom: 1rem;
}
.card-review {
  display: flex;
  margin-bottom: 1rem;
}
.rating-star {
  color: #ffaa00;
}
.total-reviews {
  font-size: 0.9rem;
  margin-left: 10px;
}
.card-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.price del {
  margin-right: 10px;
}
.bag-icon {
  color: #ff00aa;
}
